@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,200,200italic,300,300italic,900,700italic,700,600italic,600,400italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: gentona;
  src: local('gentona'), url("/static/media/Gentona Book.ffcd17af.otf") format('opentype');
}

@font-face {
  font-family: gentona;
  src: url("/static/media/Gentona Book Bold.37513f31.otf");
  font-weight: bold;
}

@font-face {
  font-family: futura;
  src: local('futura'), url("/static/media/Futura Book font.ed19af44.ttf") format('truetype');
}

@font-face {
  font-family: kgSummerSunShine;
  src: local('kgSummerSunShine'), url(/static/media/KGSummerSunshine.cb8f5087.ttf) format('truetype');
}

@font-face {
  font-family: kgSummerSunShineShadow;
  src: local('kgSummerSunShineShadow'), url(/static/media/KGSummerSunshineShadow.aed00ff2.ttf) format('truetype');
}

@font-face {
  font-family: kgSummerSunShineBlackout;
  src: local('kgSummerSunShineBlackout'), url(/static/media/KGSummerSunshineBlackout.553fc69f.ttf) format('truetype');
}
.App {
	text-align: center;
}

* {
	margin: 0;
	padding: 0;
}

body {
	font-family: Arial, Helvetica, sans-serif;
	line-height: 1.4;
}

a {
	text-decoration: none;
	color: #333;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
	overflow-x: hidden;
	display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
}


.navbar{background:transparent;height:80px;display:flex;justify-content:center;align-items:center;font-size:1.2rem}.navbar-logo{color:#fff;justify-self:start;margin-left:10px;cursor:pointer;text-decoration:none;font-size:2rem}.fa-firstdraft{margin-left:0.5rem;font-size:1.6rem}.nav-menu{display:grid;grid-template-columns:repeat(6, auto);grid-gap:8px;list-style:none;text-align:center;width:80vw;justify-content:end}.nav-item{display:flex;align-items:center;height:80px}.nav-links{color:#fff;text-decoration:none;padding:0.5rem 1rem;font-family:'gentona';font-weight:bolder}.nav-links:hover{background-color:#fff;color:#1c2237;border-radius:4px;transition:all 0.2s ease-out}.fa-bars{color:#fff}.nav-links-mobile{display:none}.menu-icon{display:none}@media screen and (max-width: 960px){.NavbarItems{position:relative}.nav-menu{display:flex;flex-direction:column;width:100%;height:85vh;position:absolute;top:80px;left:-100%;opacity:1;transition:all 0.5s ease}.nav-menu.active{background:#fff;left:0;opacity:1;transition:all 0.5s ease;z-index:2}.nav-links{text-align:center;color:#1c2237;padding:2rem;width:100%;display:table}.nav-items:hover{background-color:#1c2237;color:#fff;border-radius:0}.navbar-logo{position:absolute;top:0;left:0;transform:translate(25%, 50%)}.menu-icon{display:block;position:absolute;top:0;right:0;transform:translate(-100%, 60%);font-size:1.8rem;cursor:pointer}.fa-times{color:#fff;font-size:2rem}.nav-links-mobile{display:block;text-align:center;padding:1.5rem;margin:2rem auto;border-radius:4px;width:80%;background:#1888ff;text-decoration:none;color:#fff;font-size:1.5rem}.nav-links-mobile:hover{background:#fff;color:#1888ff;transition:250ms}button{display:none}}

